<template>
  <div>
    <page-showcase
      :heading="page && page.title ? page.title : ''"
      :text="page && page.excerpt ? page.excerpt : ''"
    />
    <v-container>
      <v-row>
        <v-col cols="8">
          <v-skeleton-loader
            v-if="$store.state.loading.page"
            type="card-avatar, article, card-actions, card-avatar, article, card-actions"
            loading
          >
          </v-skeleton-loader>
          <v-card v-if="!$store.state.loading.page && page">
            <v-card-text v-if="page.body">
              <!-- eslint-disable-next-line -->
              <div v-html="page.body"></div>
              <h1 v-if="!$store.state.loading.page && !page">Page Not Found</h1>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="4">
          <app-sidebar />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  components: {
    AppSidebar: () => import('@/components/AppSidebar'),
    PageShowcase: () => import('@/components/PageShowcase')
  },
  computed: {
    ...mapState({
      page: (state) => state.page.page
    })
  },
  watch: {
    $route() {
      this.fetchPage({ axios: this.$axios, pageSlug: this.$route.params.slug })
    }
  },
  created() {
    this.fetchPage({ axios: this.$axios, pageSlug: this.$route.params.slug })
  },
  methods: {
    ...mapActions('page', ['fetchPage'])
  }
}
</script>

